#intro {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: left;
    margin: auto;
    padding-left: 15%;
    padding-right: 5ch;
    max-width: 75rem;
    overflow: hidden;
    position: relative;
}


.bg {
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;

}

.introContent {
    height:fit-content;
    width: 100vw;
    padding: 1.5rem;
    font-size: 2.5rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: baseline;

}

.hello {
    font-size: 1.75rem;
    font-weight: 100;
}

.introName {
    color: yellow;
    margin: 0.5ch;

}

.introPara {
    margin: 1px;
    padding: 1px;
    font-size: 0.5em;
    font-weight: 30;
    letter-spacing: 1px;
}

@media screen and (max-width:840px) {
    .bg {
        width: -10vw;


    }

    .introContent {
        font-size: 8vw;
    }

    .hello {
        font-size: 4.5vw;
    }
}

@media screen and (max-width:480px) {
    .bg {
        width: -20vw;

    }
}