#contact {
    min-height: calc(100vh - 4rem);
    width: 100vw;
    /* padding: auto; */
    max-width: 60rem;

    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;

}

.clientDesc {
    font-weight: 300;
    font-size: 1rem;
}

.contactDesc {
    padding: 1rem;
    font-weight: 300;
}

.contactForm {
    margin: 1rem;
    padding:auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}

.name,
.msg,
.email {
    font-size: medium;
    width: 90%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(40, 40, 40);
}

.submitBtn {
    background: white;
    border: none;
    border-radius: 0.5rem;
    margin: 2rem;
    padding: 0.75rem 3.5rem;
}

