#footer {
    width: 100vw;
    height: auto;
    background: black;
    border: rgb(40, 40, 40);
    align-items: center;
    justify-content: center;
    font-weight: 300;
    font-size: small;
    padding-bottom: 5px;

}

.container {
    display: flex;
    /* arranging divs side by side */
}

.box {

    width: 50%;
   
    box-sizing: border-box;
   
    border: 1px;
}

.links {
    display: flex;
    padding-left: 15%;
    padding-right: 15%;
    padding-top: 10px;
    justify-content: center;
    align-items: center;

}

.contain {
    width: 100vw;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2px;
}

.link:hover .tooltiptext {
    visibility: visible;
}

.link img {
    width: 2rem;
    margin: 0.5rem;
    border-radius: 10px;
}

.footer .col ul li {

    font-size: 14px;
    padding: 5px 0px;
    letter-spacing: 0.7px;
}

.footer .col ul li:hover {
    cursor: pointer;
    color: rgb(74, 152, 230);

}

.copyright {
    font-family: Arial, sans-serif;
    font-size: 14px;
    color: #888;
    text-align: center;
    padding: 10px 0;
}

.copyright:hover {
    color: #555;
}

/* mobile view */
@media (max-width: 768px) {
    .container {
        flex-direction: column;
    }

    .box {
        justify-content: center;
        width: auto;
        padding: 20px;
        box-sizing: border-box;
        border: 1px;
    }

    .copyright {
        font-size: 18px;
    }

    .footer .col ul li {
        font-size: 18px;
        letter-spacing: 0.8px;
    }
    .links {
       padding: 8px;
       margin: 0;
       
       
    
    }
.link img {
    width: 2.3rem;
    margin: 0.5rem;
    border-radius: 10px;
}
}