#projects {
  
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 68rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
    margin: auto;
   
}

.worksTitle {
    margin: 1rem 0;
    font-size: 3rem;
}

.worksImgs {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    margin: 1px;
    padding: 1px;
    flex-wrap: wrap;
}

.worksDesc {
    margin: 300;
    font-size: 1rem;
}

.worksImg {

    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
    border-style:solid;
    border-color: rgb(26 27 32);
    border-radius: 2rem;
}

.workBtn {
    margin: 3rem 0;
    padding: 0.5rem 2.5rem;
    border: none;
    background: white;
    border-radius: 2rem;
    font-size: 1rem;
}

@media screen and (max-width:585px) {
    .worksImg {
        height: 48vw;
    }

}