#skills {

    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top:1em;
    margin-bottom:3em;
}

.skillTitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    
}

.skillDesc {
    font-weight: 400;
    font-size: 1rem;
    max-width: 51rem;
    padding: 0.2rem;
}

.skillBars {
    margin-bottom: 1em;
    width: 100vw;
    max-width: 80%;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.skillBar {
    padding-left: 0.5em;
    padding-right: 0.8em;
    padding-bottom: 0.4em;
    padding-top: 0.6em;
    margin: 1em;

    border-radius: 0.5rem;
    background: black;
}

.skillBarImg {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}

.skillBarText>p {
    font-size: 0.9rem;
    letter-spacing: 0.1rem;
    font-family: monospace;
    font-weight: 200;
}

@media screen and (max-width:720px) {
    .skillBarText>p {
        font-size: 2vw;
        /* font-weight: 0.9rem; */
    }

}

@media screen and (max-width:480px) {
    .skillBarText>p {

        font-size: 3vw;
        /* font-weight: 0.9rem; */
    }

    .skillBarImg {
        height: 2.25rem;
        width: 2.25rem;

    }

    .skillBarText>h2 {

        font-size: 5vw;
        /* font-weight: 0.9rem; */
    }

}