.navbar {
    /* background-color: rgb(30, 30, 30); */
    background: rgb(48, 49, 54);
    height: 4rem;
    width: 100vw;
    max-width: 75rem;
    margin: 2px;
    padding: 0.7em;
    padding-right: 2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;


}

.logo {
    object-fit: cover;
    height: 4rem;
    width: 4rem;
}

.desktopMenuListItem {
    color: azure;
    margin: 1rem;
    cursor: pointer;
}

.desktopMenuListItem:hover {
    color: rgb(87, 184, 244);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(10, 115, 180);
}

.desktopMenuBtn {
    background: white;
    color: black;
    border: none;
    display: flex;
    height: 2.5em;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    border: 3px solid  rgb(48, 49, 54);
}
.desktopMenuBtn:hover {
    background: white;
    color: rgb(39, 95, 238);
    border-bottom: 3px solid rgb(10, 115, 180);
    
}


.desktopMenuImg {
    object-fit: cover;
    height: 1em;
    width: 1em;
    margin: 0.8em;

}


.btn {
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 2rem;

}

.btnImg {
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

.active {
    color: rgb(31, 171, 21);
    padding-bottom: 0.5rem;
    border-bottom: 1px solid rgb(40, 193, 35);
}



.navMenu {
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: none;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-height:fit-content;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}

.listItem {
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
}

.mobMenu {
    display: none;
    object-fit: cover;
    height:50%;
}

@media screen and (max-width:720px) {
    .mobMenu {
        display: flex;
    }

    .desktopMenuBtn {
        display: none;
    }

    .desktopMenu {
        display: none;
    }
}